@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root{
  --font-default: 'Roboto', sans-serif;
  --color-default: #000000;
  --color-alt1: #1e2a3e;
  --color-alt2: #ffffff;
  --color-alt3: #33435c;
  --color-alt4: #dde2e4;
  --color-alt5: #cccccc;
  --color-alt6: #edf0f2;
  --color-alt7: #3e85cf;
}
*, :after, :before {
    box-sizing: border-box;
}
.gist-item{
    border: 1px solid #cccccc;
    border: 1px solid var(--color-alt5);
    border-radius: 5px;
    margin: 10px auto;
    overflow: hidden;
    padding: 10px;
    background: #edf0f2;
    background: var(--color-alt6);
    font-weight: 300;
    font-size: .8em;
    min-width: 250px;
    max-width: 300px;
    flex: 0 0 33%;
}
.gist-item > *{
    padding: 5px 0;
}
.gist-item a{
    display: inline-block;
    text-decoration: none;
    font-size: 1.2em;
    font-weight: 600;
    color: #33435c;
    color: var(--color-alt3);
}
.gist-item a:hover{
    text-decoration: underline;
    color: #3e85cf;
    color: var(--color-alt7);
}
.label{
    font-weight: 600;
    padding-right: 5px;
}
.tags{
    display: flex;
    align-items: center;
    overflow: hidden;
}
.tag {
    display: inline-flex;
    align-items: center;
    padding-right: 5px;
}
.tag span{
    width: 12px;
    height: 12px;
    border: 1px solid #ccc;
    border-radius: 50%;
    background: #ccc;
    display: inline-block;
    margin-right: 3px;
}
.forks {
    display: flex;
    overflow: hidden;
}

html,body,#root{margin: 0; padding: 0; width: 100%; height: 100%;}
#root{
  font-family: var(--font-default);
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: stretch;
}
header > *, main > *, footer > *{
  padding: 0 25%;
}
header{
  flex: 0 0 auto;
}
.searchbar{
    padding: 20px 25%;
    background: var(--color-alt3);
    display: flex;
    height: 80px;
    align-items: center;
}
.searchbar input{
    flex: 0 0 300px;
    border: 1px solid var(--color-alt2);
    border-radius: 40px;
    height: 40px;
    padding: 15px;
    margin-right: 10px;
}
.searchbar .search-btn{
    display: inline-flex;
    width: 40px;
    height: 40px;
    padding: 5px;
}
.searchbar .search-btn:hover{
    cursor: pointer;
}
.title{
  background: var(--color-alt1);
  color: var(--color-alt2);
}
.searchbar{
  padding: 20px 25%;
  background: var(--color-alt3);
}
main{
  flex: 1 1 auto;
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
}
main::-webkit-scrollbar{
  width: 0;
}
.listing{
    display: flex;
    flex-wrap: wrap;
}
footer{
  flex: 0 0 auto;
  background: var(--color-alt4);
  padding: 20px 0;
  text-align: center;
  font-weight: 100;
}
h1{
  margin: 0;
  padding: 20px 0;
  border-bottom: 1px solid var(--color-alt5);
}
p{
  margin: 0;
  padding: 20px 0;
}
